$bg: lightseagreen;
$text: white;
$link: hotpink;
$highlight: white;
$dg: rgba(4, 25, 24, 0.5);
$wg: rgba(255, 255, 255, 0.2);
$bgo: rgba(32, 178, 170, 0.5);

@import url("https://fonts.googleapis.com/css2?family=Alegreya:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Gruppo&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Share:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@100;400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bitter:ital,wght@0,400;0,700;0,900;1,800&display=swap");
:root {
    --clr-link: radial-gradient(
        circle,
        rgba(179, 50, 156, 1) 50%,
        rgba(32, 178, 170, 1) 100%
    );
    // --ff-heading: "Alegreya", serif;
    // --ff-heading: "Big Shoulders Display", cursive;
    --ff-heading: "Bitter", serif;
    // --ff-text: "Gruppo", cursive;
    --ff-text: "Share", cursive;
    --ff-default: system-ui, -apple-system, BlinkMacSystemFont,
        ".SFNSText-Regular", sans-serif;
    --shadow-color: 0deg 0% 0%;
    --shadow-elevation-low: 1px 1px 1.1px hsl(var(--shadow-color) / 0.31),
        1.5px 1.5px 1.6px -2.5px hsl(var(--shadow-color) / 0.21),
        5px 5px 5.3px -5px hsl(var(--shadow-color) / 0.1);
    --shadow-elevation-medium: 1px 1px 1.1px hsl(var(--shadow-color) / 0.26),
        1.4px 1.4px 1.5px -1.2px hsl(var(--shadow-color) / 0.21),
        4px 4px 4.2px -2.5px hsl(var(--shadow-color) / 0.16),
        11.1px 11.1px 11.8px -3.7px hsl(var(--shadow-color) / 0.11),
        25px 25px 26.5px -5px hsl(var(--shadow-color) / 0.05);
    --shadow-elevation-high: 1px 1px 1.1px hsl(var(--shadow-color) / 0.24),
        1.2px 1.2px 1.3px -0.6px hsl(var(--shadow-color) / 0.22),
        2.6px 2.6px 2.8px -1.1px hsl(var(--shadow-color) / 0.2),
        6.5px 6.5px 6.9px -1.7px hsl(var(--shadow-color) / 0.17),
        14.1px 14.1px 15px -2.2px hsl(var(--shadow-color) / 0.15),
        26.5px 26.5px 28.1px -2.8px hsl(var(--shadow-color) / 0.12),
        45.1px 45.1px 47.8px -3.3px hsl(var(--shadow-color) / 0.1),
        71.1px 71.1px 75.4px -3.9px hsl(var(--shadow-color) / 0.07),
        105.6px 105.6px 112px -4.4px hsl(var(--shadow-color) / 0.05),
        150px 150px 159.1px -5px hsl(var(--shadow-color) / 0.02);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
*::before,
*::after {
    box-sizing: border-box;
}
img,
picture,
video,
canvas,
svg {
    display: block;
    max-width: 100%;
}
input,
button,
textarea,
select {
    font: inherit;
}
p,
h1,
h2,
h3,
h4,
h5,
h6 {
    overflow-wrap: break-word;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: var(--ff-heading);
}
body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
    // overflow: hidden;
    background-color: $dg;
    background-image: url("/assets/img/bigbg.webp");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    color: $text;
    width: 100%;
    margin: 0;
    padding: 0;
}
footer {
    min-height: 5rem;
    color: $text;
    background-color: $dg;
    // display: inline-flex;
    text-align: right;
    line-height: 5rem;
    padding: 0 10vw;
    margin: 0;
}
nav {
    background-color: $dg;
    padding: 0.5rem;
    position: fixed;
    width: 100%;
    overflow: hidden;
    box-shadow: var(--shadow-elevation-high);
    // border: 1px solid pink;
    top: 0;
    a {
        font-weight: bold;
        font-size: 2rem;
        text-decoration: none;
        color: $link;
        border-bottom: 0.3rem solid $text;
        display: inline-flex;
        margin: 0;
        padding: 0;
        line-height: 2rem;
    }
    a.check {
        padding-right: 1px;
    }
    .nav-icon {
        width: 2rem;
        fill: $link;
    }
}
.content {
    background-color: $dg;
    box-shadow: var(--shadow-elevation-high);
    // overflow-y: scroll;
    width: 100%;
    height: calc(100% - 10vh);
    display: flex;
    flex-direction: row;

    align-items: stretch;
    justify-content: center;
    margin: 0;
    margin-top: clamp(4rem, 10vh, 2rem);
    margin-bottom: clamp(1rem, 10vh, 0.5rem);

    .about_section {
        box-shadow: var(--shadow-elevation-low);
        padding: 0 2rem;
        // background-color: $bg;
        background: rgb(32, 178, 170);
        background: linear-gradient(
            180deg,
            rgba(32, 178, 170, 1) 0%,
            rgba(32, 178, 170, 0.5) 100%
        );
    }
    .card {
        box-shadow: var(--shadow-elevation-low);
        // & > * {
        //     //debug
        //     outline: 0.1rem solid $link;
        // }
        background-color: $wg;
        display: inline-block;
        text-align: right;
        max-width: calc(50vw-2rem);
        p,
        h1,
        ul {
            padding: 0 0.5rem;
        }
        .avatar {
            transition: all 0.5s;
            transform: scale(0.8);
            border-radius: 25%;
            padding: 10%;
            &:hover {
                transform: rotate(13deg);
                border-radius: 50%;
            }
            &::before {
                content: "";
                display: block;
                background: $dg;
                width: inherit;
                border: 1rem solid $link;
            }
        }
        h1 {
            color: $link;
            background-color: $bg;
            line-height: 1.5rem;
            font-size: 1.5rem;
            font-family: var(--ff-heading);
            margin-bottom: 0.5rem;
        }
        p {
            line-height: 1rem;
            font-size: 1rem;
            font-family: var(--ff-text);
            margin-bottom: 0.5rem;
        }
        ul {
            list-style-type: none;
            padding-bottom: 0.5rem;
            li {
                display: inline-flex;
                a {
                    color: $text;
                    display: inline-block;
                    font-weight: bold;
                    line-height: 1rem;
                    padding: 0 0.3rem;
                    border-radius: 0.3rem;
                    background-color: $bgo;
                    box-shadow: var(--shadow-elevation-low);
                    text-decoration: none;
                    border-bottom: 0.3rem solid $highlight;
                    &:visited {
                        border-bottom: 0.3rem solid $link;
                    }
                    &:focus,
                    &:hover {
                        color: $link;
                        border-bottom: 0.3rem solid $highlight;
                    }
                }
            }
        }
    }
    hr {
        border-bottom: 0.5rem $bgo double;
    }
    h1 {
        text-transform: uppercase;
    }
    p {
        font-size: 1.2rem;
        line-height: 1.2rem;
        font-family: var(--ff-text);
    }
    article {
        // background-color: $dg;
        padding: 2rem;
        width: 100%;
        header {
            display: flex;
            align-items: center;
            text-align: center;
            justify-content: center;
            background: $dg;
            border-left: 0.3rem solid $link;
            border-bottom: 0.3rem solid $link;
            border-right: 0.3rem solid $bg;
            border-top: 0.3rem solid $bg;
            box-shadow: var(--shadow-elevation-high);
            time {
                color: $link;
            }
        }
        main {
            padding: 1rem;
            border-right: 0.3rem solid $bg;
            border-bottom: 0.3rem solid $bg;
            border-left: 0.3rem solid $link;

            box-shadow: var(--shadow-elevation-high);

            p {
                // padding: 0 0.5rem;
                line-height: 1.5rem;
                margin-bottom: 1rem;
            }
            pre {
                overflow: auto;
            }
            h1,
            h2,
            h3 {
                // padding: 0 0.5rem;
                margin-bottom: 1rem;
            }
            h1 {
                border-bottom: 0.5rem double $bg;
                font-weight: 700;
            }
            h2 {
                border-bottom: 0.5rem double $link;
            }
            h3 {
                border-bottom: 0.5rem double $highlight;
            }
            a {
                display: inline-block;
                font-weight: bold;
                line-height: 1rem;
                padding: 0 0.3rem;
                border-radius: 0.3rem;
                background-color: $bgo;
                box-shadow: var(--shadow-elevation-low);
                text-decoration: none;
                border-bottom: 0.3rem solid $highlight;
                &:visited {
                    border-bottom: 0.3rem solid $link;
                }
                &:focus,
                &:hover {
                    color: $link;
                    border-bottom: 0.3rem solid $highlight;
                }
            }
            .image {
                width: 50%;
                float: right;
                height: auto;
                margin-left: 1rem;
                overflow: hidden;
                background-color: $wg;
                // &:not(:last-of-type) {
                //     margin-bottom: 1rem;
                // }
                img {
                    display: block;
                    object-fit: contain; /*cover for filled area*/
                    width: 100%;
                    height: 100%;
                }
            }
            .image.land {
                aspect-ratio: 16/9;
            }
            .image.port {
                aspect-ratio: 9/16;
            }
        }
        ul {
            margin-bottom: 1rem;
            li {
                font-family: var(--ff-text);

                font-size: 1.2rem;
                line-height: 1.1rem;
                list-style-position: inside;
                list-style-type: square;
                & > ul > li {
                    padding-left: 1rem;
                    &::marker {
                        color: $bg;
                        font-weight: bold;
                    }
                }
                &::marker {
                    color: $link;
                    font-weight: bold;
                }
            }
        }

        code {
            border: 0.1rem solid $bgo;
            background-color: $dg;
            padding: 0rem 0.3rem;
            margin: 0.1rem;
            text-align: left;
            display: inline-block;
            color: $highlight;
        }
        pre {
            font-size: 1rem;
        }
    }

    a {
        text-decoration: none;
        // text-align: right;
        color: $link;
        // border-bottom: 0.2rem solid $text;
        display: inline-flex;
        margin: 0;
        padding: 0;
        // background-color: $bgo;
    }
    a:visited {
        color: $bg;
        background-color: $dg;
    }
    ul.listPosts {
        // background-color: $dg;
        list-style-type: none;
        padding: 1rem;
        li {
            background-color: $dg;
        }
        li:nth-child(odd) {
            text-align: left;
            border-left: 0.3rem solid $link;
            border-bottom: 0.3rem solid $link;
        }
        li:nth-child(even) {
            text-align: right;
            border-bottom: 0.3rem solid $bg;
            border-right: 0.3rem solid $bg;
        }
    }
    .side-bar {
        h1 {
            display: block;
            width: auto;
        }
        .icon-header {
            display: block;
            // width: 10rem;
            max-width: 50%;
        }
    }
    .post-excerpt {
        padding: 0 1rem;
        &:first-of-type {
            border-top: 0.3rem solid $bg;
        }
    }
}
/** place to put media q stuff**/

@media (max-width: 22rem) and (orientation: portrait) {
    nav {
        a {
            span {
                transition: all 0.5s;
                // transition-delay: 0.1s;
                display: none;
                opacity: 0;
                // visibility: hidden;
            }
        }
        a.check {
            padding-right: 0px;
        }
    }
    .content {
        flex-direction: column;
    }
}
@media (max-width: 40rem) {
    .content article main .image {
        margin: 0;
        float: none;
        display: inline-block;
        width: 100%;
    }
    .content {
        flex-direction: column;
    }
}
// @media (min-width: 22rem) and (max-width: 44rem) {
//     .content {
//         flex-direction: column;
//     }
//     main > .image {
//         float: none;
//     }
// }

// big screen
@media (min-width: 60rem) {
    .content {
        padding: 0 15vw;
        flex-direction: row;
    }
    nav {
        padding: 1vh 15vw;
        a:first-of-type {
            margin-left: 2rem;
        }
    }
}
